import React from 'react'
import CaseStudyDetailData from './CaseStudyDetailPageData'
import BannerOliv from '../../../components/BannerOliv'
import Container from 'Shared/components/Container'
import HtmlParser from 'react-html-parser'
import './CaseStudyDetailPage.scss'
import StoriesCardSection from '../StoriesCardSection/StoriesCardSection'
import CaseStudyCTA from '../CaseStudyCTA/CaseStudyCTA'
import bgImage from 'Shared/assets/images/case-studies/cta.jpg'

const CaseStudyDetailPage = ({ match }) => {
  const data = CaseStudyDetailData.find((caseStudy) => caseStudy.slug === match.params.slug)
  console.log(data)
  return (
    <div>
      <BannerOliv
        bgImage={data.banner.bgImage}
        image={data.banner.cardLogo}
        title={data.banner.title}
        features={data.banner.features}
        innerBanner
      />
      <div className='content_page'>
        <Container>
          {HtmlParser(data.content.pageContent)}
        </Container>
      </div>
      <StoriesCardSection limit />
      <CaseStudyCTA
        title={`Getting started is easy.<br />And completely free, forever.`}
        des={`Tap into a screened community, use technology built <br />for relevancy, and get unparalleled hiring support.`}
        bgImage={bgImage}
        buttonsOne={
          {
            id: 1,
            name: 'Post a Job',
            link: '/candidate/sign-up'
          }
        }
        buttonsTwo={
          {
            id: 1,
            name: 'Contact us',
            link: '/contact'
          }
        }
      />
    </div>
  )
}

export default CaseStudyDetailPage



